$global-font-family: $g-font;
$global-gutter: 20px;
$global-medium-gutter: 20px;

$breadcrumb-divider: "\\";
$breadcrumb-divider-margin-horizontal: 5px;
$breadcrumb-item-color: #C4C4C4;
$breadcrumb-divider-color: #C4C4C4;
$breadcrumb-item-active-color: #C4C4C4;
$breadcrumb-item-hover-color: $g-col-main;

$pagination-margin-horizontal: 15px;
$pagination-item-color: $g-col-title;
// $pagination-item-hover-color: $global-color !default;
// $pagination-item-hover-text-decoration: none !default;
// $pagination-item-active-color: $global-color !default;
