// 2. Import default variables and available mixins.
@import "../uikit-lib/src/scss/variables-theme.scss";
@import "../uikit-lib/src/scss/mixins-theme.scss";

// 3. Your custom mixin overwrites.
@mixin hook-breadcrumb(){
	margin: 30px 0;
}
@mixin hook-breadcrumb-item(){
	@include fluidFontSize(12px, 14px);
}

@mixin hook-pagination(){
	justify-content: center;
}
@mixin hook-pagination-item(){ 
	display: flex;
	align-items: center;
	justify-content: center;
	width: 60px;
	height: 60px;
	border: 1px solid $g-col-text;
	transition: $g-anime;
	font-weight: 500;
	@include fluidFontSize(30px);

	&.arrow-left {
		border: none;
		width: 30px;
		&:after {
			left: 45%;
			border-color: $g-col-title;
			padding: 5px;
		}
	}

	&.arrow-right {
		border: none;
		width: 30px;

		&:after {
			right: 45%;
			border-color: $g-col-title;
			padding: 5px;
		}
	}
}
@mixin hook-pagination-item-hover(){}
@mixin hook-pagination-item-active(){}
@mixin hook-pagination-item-disabled(){}
@mixin hook-pagination-misc(){}

// 4. Import UIkit.
@import "../uikit-lib/src/scss/uikit-theme.scss";
